import React, { useContext } from 'react'
import { useWeb3React } from '@web3-react/core'
import { LanguageContext } from 'hooks/LanguageContext'
import useGetPriceData from 'hooks/useGetPriceData'
import useGetLocalProfile from 'hooks/useGetLocalProfile'
import useAuth from 'hooks/useAuth'
import useMetamask from 'hooks/useMetamask'

const Header: React.FC = (props) => {
  const { account } = useWeb3React()
  let address: string;
  const connect = () => {
    const div: HTMLElement = document.getElementsByClassName('sc-eGCarw hZuNae')[0] as HTMLElement
    div.click();
  }
  const addMeta = () => {
    const div: HTMLElement = document.getElementsByClassName('sc-dlfnbm fbfZzW sc-bTvRPi FZRzF')[0] as HTMLElement
    div.click();
  }
  if(account) {
    address = account.slice(0, 3).concat( "...").concat( account.slice(account.length - 3));
  }
  else address = "Connect";

  return (
    <nav className="navbar navbar-expand-lg navbar-dark">
      <a className="navbar-brand" href="/"> swap </a>
      <div className="wrapper">
        <button onClick={() => addMeta()} type="button" className="btn btn-primary rounded-pill">PriceAI</button>
        <button onClick={() => connect()} type="button" className="btn btn-primary rounded-pill">{address}</button>
      </div>
    </nav>
  )
}

export default Header
